import React, { useMemo, useRef } from 'react';
import { useDrop, useDrag } from 'react-dnd';
import {
	ConstructorElement,
	Button,
	CurrencyIcon,
	DragIcon
} from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './burger-constructor.module.css';
import { ItemTypes } from '../../utils/dnd-types';
import {
	addIngredient,
	removeIngredient,
	moveIngredient
} from '../../services/constructor/slice';
import { createOrder } from '../../services/order/slice';
import {useLocation, useNavigate} from "react-router-dom";
import { Ingredient } from '../../utils/types';
import {useAppDispatch, useAppSelector} from "../../hooks/redux";

interface DraggableConstructorElementProps {
	item: Ingredient & { id: string };
	index: number;
	moveCard: (dragIndex: number, hoverIndex: number) => void;
	onRemove: () => void;
}

const DraggableConstructorElement = ({ item, index, moveCard, onRemove }: DraggableConstructorElementProps) => {
	const ref = useRef<HTMLDivElement>(null);

	const [{ isDragging }, drag] = useDrag({
		type: ItemTypes.CONSTRUCTOR_INGREDIENT,
		item: () => ({ id: item.id, index }),
		collect: (monitor) => ({
			isDragging: monitor.isDragging()
		})
	});

	const [, drop] = useDrop({
		accept: ItemTypes.CONSTRUCTOR_INGREDIENT,
		hover: (draggedItem: { id: string; index: number }, monitor) => {
			if (!ref.current) return;

			const dragIndex = draggedItem.index;
			const hoverIndex = index;

			if (dragIndex === hoverIndex) return;

			const hoverBoundingRect = ref.current.getBoundingClientRect();
			const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
			const clientOffset = monitor.getClientOffset();
			const hoverClientY = clientOffset!.y - hoverBoundingRect.top;

			if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) return;
			if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) return;

			moveCard(dragIndex, hoverIndex);
			draggedItem.index = hoverIndex;
		}
	});

	drag(drop(ref));

	const opacity = isDragging ? 0 : 1;

	return (
		<div
			ref={ref}
			className={styles.draggableItem}
			style={{ opacity }}
			data-testid={`constructor-item-${item.id}`}
		>
			<DragIcon type="primary" />
			<ConstructorElement
				text={item.name}
				price={item.price}
				thumbnail={item.image}
				extraClass={styles.ingredient}
				handleClose={onRemove}
			/>
		</div>
	);
};

const BurgerConstructor = () => {
	const dispatch = useAppDispatch();
	const { bun, ingredients } = useAppSelector(state => state.burgerConstructor);
	const { loading: orderLoading } = useAppSelector(state => state.order);
	const { items: allIngredients } = useAppSelector(state => state.ingredients);
	const navigate = useNavigate();
	const location = useLocation();

	const { isAuthChecked, accessToken } = useAppSelector(state => state.auth);

	const [, dropTarget] = useDrop({
		accept: ItemTypes.INGREDIENT,
		drop(item: { id: string }) {
			const ingredient = allIngredients.find((ing) => ing._id === item.id);
			if (ingredient) {
				dispatch(addIngredient(ingredient));
			}
		}
	});

	const totalPrice = useMemo(() => {
		const bunPrice = bun ? bun.price * 2 : 0;
		const ingredientsPrice = ingredients.reduce(
			(sum, item) => sum + item.price,
			0
		);
		return bunPrice + ingredientsPrice;
	}, [bun, ingredients]);

	const handleOrderClick = () => {
		if (!isAuthChecked || !accessToken) {
			navigate('/login', { state: { from: location.pathname } });
			return;
		}

		if (!bun) return;

		const ingredientsIds = [
			bun._id,
			...ingredients.map((item) => item._id),
			bun._id
		];

		dispatch(createOrder(ingredientsIds));
	};

	return (
		<section
			className={`${styles.constructor} mt-25 pl-4 pr-4`}
			ref={dropTarget}
			data-testid="burger-constructor"
		>
			{bun && (
				<ConstructorElement
					type="top"
					isLocked={true}
					text={`${bun.name} ()`}
					price={bun.price}
					thumbnail={bun.image}
					extraClass={`${styles.bun} ${styles.fixedSize}`}
				/>
			)}

			<div className={`${styles.scrollableList} custom-scroll`}>
				{ingredients.map((item, index) => (
					<DraggableConstructorElement
						key={item.id}
						item={item}
						index={index}
						moveCard={(dragIndex, hoverIndex) =>
							dispatch(moveIngredient({ dragIndex, hoverIndex }))
						}
						onRemove={() => dispatch(removeIngredient(item.id))}
					/>
				))}
			</div>

			{bun && (
				<ConstructorElement
					type="bottom"
					isLocked={true}
					text={`${bun.name} ()`}
					price={bun.price}
					thumbnail={bun.image}
					extraClass={`${styles.bun} ${styles.fixedSize}`}
				/>
			)}

			<div className={`${styles.total} mt-10`}>
				<div className={`${styles.price} mr-10`}>
					<span className="text text_type_digits-medium mr-2">{totalPrice}</span>
					<CurrencyIcon type="primary" />
				</div>
				<Button
					htmlType="button"
					type="primary"
					size="large"
					onClick={handleOrderClick}
					disabled={!bun || orderLoading}
				>
					{orderLoading ? '...' : ' '}
				</Button>
			</div>
		</section>
	);
};

export default BurgerConstructor;