import { Link, useNavigate } from 'react-router-dom';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useForm } from '../../hooks/use-form';
import { registerUser } from '../../services/auth/slice';
import React, { useEffect, useState } from 'react';
import styles from './register.module.css';
import {useAppDispatch, useAppSelector} from "../../hooks/redux";

interface FormValues {
	name: string;
	email: string;
	password: string;
}

const RegisterPage = () => {
	const dispatch = useAppDispatch();
	const navigate = useNavigate();
	const { user, loading, error } = useAppSelector(state => state.auth);
	const { values, handleChange } = useForm<FormValues>({
		name: '',
		email: '',
		password: ''
	});
	const [showPassword, setShowPassword] = useState(false);

	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		dispatch(registerUser(values));
	};

	useEffect(() => {
		if (user) {
			navigate('/', { replace: true });
		}
	}, [user, navigate]);

	return (
		<div className={styles.container}>
			<h1 className={`text text_type_main-medium ${styles.title}`}></h1>
			{error && <p className={`text text_type_main-default ${styles.error}`}>{error}</p>}
			<form className={styles.form} onSubmit={handleSubmit}>
				<Input
					type="text"
					placeholder=""
					name="name"
					value={values.name}
					onChange={handleChange}
					extraClass="mb-6"
					onPointerEnterCapture={() => {}}
					onPointerLeaveCapture={() => {}}
				/>
				<Input
					type="email"
					placeholder="E-mail"
					name="email"
					value={values.email}
					onChange={handleChange}
					extraClass="mb-6"
					onPointerEnterCapture={() => {}}
					onPointerLeaveCapture={() => {}}
				/>
				<Input
					type={showPassword ? 'text' : 'password'}
					placeholder=""
					name="password"
					value={values.password}
					onChange={handleChange}
					icon={showPassword ? 'HideIcon' : 'ShowIcon'}
					onIconClick={() => setShowPassword(prev => !prev)}
					extraClass="mb-6"
					onPointerEnterCapture={() => {}}
					onPointerLeaveCapture={() => {}}
				/>
				<Button
					htmlType="submit"
					type="primary"
					size="medium"
					extraClass="mb-20"
					disabled={loading}
				>
					{loading ? '...' : ''}
				</Button>
			</form>
			<p className="text text_type_main-default text_color_inactive mb-4">
				 ?{' '}
				<Link to="/login" className={styles.link}>
					
				</Link>
			</p>
		</div>
	);
};

export default RegisterPage;